import { reduxForm, Field } from "redux-form";
import { Input } from "../../../commom/FormControls/formControls";
import { requiredField, numberField,mailField } from "../../../utils/validarors";

const Form = (props) => {
  
  return (
    <form>
      <div className="card mb-4">
        <div className="card-header">Данные о заявке</div>
        <div className="card-body">
          <div className="row mb-3">
            <div className="col-md-2">
              <strong>ID:</strong>
            </div>
            <div id="number" className="col">
              Заявка №{props.id}
            </div>
          </div>
          <div className="row mb-3">
            <div className="col-md-2">
              <strong>Дата создания:</strong>
            </div>
            <div id="date" className="col">
              {props.dateTime}
            </div>
          </div>
          <div className="row mb-3">
            <div className="col-md-2">
              <strong>Продукт:</strong>
            </div>
            <div className="col">
              <Field
                validate={[]}
                name="product"
                component="select"
                className="custom-select"
              >
                <option value="course-html">Курс по верстке</option>
                <option value="course-js">Курс по JavaScript</option>
                <option value="course-vue">Курс по VUE JS</option>
                <option value="course-php">Курс по PHP</option>
                <option value="course-wordpress">Курс по WordPress</option>
              </Field>
            </div>
          </div>
          <div className="row mb-3">
            <div className="col-md-2">
              <strong>Имя:</strong>
            </div>
            <div className="col">
              <Field
                validate={[requiredField]}
                name="name"
                className="form-control"
                placeholder="Петр Сергеевич"
                component={Input}
              />
            </div>
          </div>
          <div className="row mb-3">
            <div className="col-md-2">
              <strong>Email:</strong>
            </div>
            <div className="col">
              <Field
                name="email"
                validate={[requiredField, mailField]}
                className="form-control"
                component={Input}
              />
            </div>
          </div>
          <div className="row mb-3">
            <div className="col-md-2">
              <strong>Телефон:</strong>
            </div>
            <div className="col">
              <Field
                name="phone"
                validate={[requiredField, numberField]}
                className="form-control"
                component={Input}
              />
            </div>
          </div>
          <div className="row mb-3">
            <div className="col-md-2">
              <strong>Статус заявки:</strong>
            </div>
            <div className="col">
              <Field
                validate={[requiredField]}
                name="status"
                component="select"
                className="custom-select"
              >
                <option value="">Выберите...</option>
                <option value="new">Новая</option>
                <option value="work">В работе</option>
                <option value="payment-expected">Ожидается оплата</option>
                <option value="completed">Завершена</option>
                <option value="rejection">Отказ</option>
              </Field>
            </div>
          </div>
        </div>
      </div>

      <div className="row justify-content-between">
        <div className="col">
          <button onClick={props.handleSubmit} className="btn btn-primary">
            Сохранить изменения
          </button>
        </div>
        <div className="col text-right">
          <button disabled = {props.arhive} type="button" onClick={()=>props.addArhive(props.client)}
            className="btn btn-dark">
            Удалить в архив
          </button>
            <button onClick={props.deleteClient} style = {{margin: "5px"}}
             type="button"
             className="btn btn-dark">
            Удалить
          </button>
        </div>
      </div>
    </form>
  );
}


 const ReduxForm = reduxForm({
  form: 'application'
 })(Form)




export default ReduxForm;